<script>
const cates = ['办公效率', '职业发展', '编程开发', '产品和设计', '生活方式', '亲子教育', '语言学习']
const list = []
for (let index = 0; index < 13; index++) {
  list.push({
    id: index + 1,
    publishTime: '2021-04-20 13:' + (60 - index),
    materialType: index % 3 === 1 ? 1 : 2,
    title: 'B模块 嵌入式编程1' + (50 - index),
    cate: cates[index % cates.length],
    type: 1,
    staus: index % 3,
  })
}
export default {
  data() {
    return {
      resType: 1,
      optionList: [],
      list: [],
    }
  },
  methods: {
    onOptionChange() {},
    onTabChange(key) {
      this.list = key === 1 ? [...list] : []
    },
  },
  created() {
    this.list = list
  },
}
</script>
